import React, { createElement, Fragment, useEffect, useMemo, useState } from 'react'
import { Link, Element } from 'react-scroll'
import { useMeasure, useWindowSize, useWindowScroll } from 'react-use'
import { useDisclosure } from "@chakra-ui/react"
import { graphql, useStaticQuery } from 'gatsby'
import loadable from '@loadable/component'

import Container from '../../components/Container'
import Text from '../../components/Text'
import Box from '../../components/Box'
import { responsive } from '../../components/ThemeProvider/theme';

import Layout from '../Layout';

import Banner from './Banner'
import BottomNav from './BottomNav'
import { trackView } from '../../utils/trackEvent'

const VideoModal = loadable(() => import('../VideoModal'))
const FocusOne = loadable(() => import('./FocusOne/Loadable'))
const FocusTwo = loadable(() => import('./FoucusTwo/Loadable'))
const NewMarket = loadable(() => import('./NewMarket/Loadable'))
const Esg = loadable(() => import('./Esg/Loadable'))
const KeyPoint = loadable(() => import('./KeyPoint/Loadable'))
const Fund = loadable(() => import('./Fund/Loadable'))
const Buy = loadable(() => import('./Buy/Loadable'))

const pages = [
  'section-focus',
  'ESG-green',
  'new-market',
  'target',
  'section-subject',
  'section-fund',
  'section-buy'
]

const titles = [
  '',
  'ESG',
  '',
  '',
  'ESG',
  '',
  '',
].reduce((obj, n, i) => {
  obj[pages[i]] = n
  return obj
}, {})

const comps = [
  KeyPoint,
  FocusOne,
  FocusTwo,
  NewMarket,
  Esg,
  Fund,
  Buy,
]

const ProgressLoad = ({ page }) => {
  const [loaded, setLoaded] = useState(0)
  return useMemo(() => comps.map((p, i) => {
    if (loaded < i) return null
    const show = page === pages[i]
    return (
      <Element name={pages[i]} key={i}>
        {createElement(p, { show, onLoad: setLoaded(i + 1) })}
      </Element>
    )
  }), [loaded, page])
}

const IndexPage = () => {
  const { site: { siteMetadata } } = useStaticQuery(graphql`
    query HomeQuery {
      site {
        siteMetadata {
          title
          description
          url
        }
      }
    }
  `)
  const [ref, { height }] = useMeasure();
  const [page, setPage] = useState()
  const { isOpen, onOpen, onClose } = useDisclosure()
  const windowSize = useWindowSize()
  const { y } = useWindowScroll();
  const [windowHeight, setWindowheight] = useState(windowSize.height)
  useEffect(() => {
    // 
    if (Math.abs(windowSize.height - windowHeight) > 80) {
      setWindowheight(windowSize.height)
    }
  }, [windowSize.height, windowHeight])
  useEffect(() => {
    if (page) {
      trackView(page, `${titles[page]} | ${siteMetadata.title}`)
    }
  }, [page, siteMetadata])
  const overBanner = useMemo(() => y > windowHeight, [y, windowHeight])
  return (
    <Layout onOpen={onOpen} overBanner={overBanner} siteMetadata={siteMetadata}>
      <Box.Absolute top="-9999px" left="-9999px" fontSize="0" zIndex={-999}>
        {/* <Link to="intro" spy onSetActive={(to) => setPage(to)}/> */}
        {pages.map((name) => (
          <Link
            spy
            offset={-360}
            to={name}
            onSetActive={(to) => setPage(to)}
            key={name}
            hashSpy
          />
        ))}
      </Box.Absolute>
      <Box
        as={Element}
        name="intro"
        height={responsive(`calc(100% - ${height}px)`, '100%')}
      >
        <Banner />
      </Box>
      <ProgressLoad page={page} />
      <Container fontWeight="bold" py="3em" fontSize="0.875em">
         |()
        <Text.Inline color="custom.orange"></Text.Inline>
        
        <Text.Inline color="custom.orange">Rule 144A(CDSCDX indexiTraxx Index)</Text.Inline>
        <Text.Inline color="custom.orange">N</Text.Inline>
      </Container>
      <BottomNav onOpen={onOpen} ref={ref} />
      <VideoModal onClose={onClose} isOpen={isOpen} />
    </Layout>
  )
}

export default IndexPage
